import { Product } from './product.model';
import { ProductionLine } from './production-line.model';

export class ProductionProduct {

   private _productionAllLines: Array<ProductionLine> = [];
   private _productionInLines: Array<ProductionLine> = [];
   private _productionOutLines: Array<ProductionLine> = [];
   private _production: number = 0;
   private _productionTotal: number = 0;
   private _productionInTotal: number = 0;
   private _productionOutTotal: number = 0;

   constructor(private _product: Product){
   }

   public get product() : Product {
      return this._product;
   }

   public get productionAllLines() : Array<ProductionLine> {
      return this._productionAllLines;
   }

   public get productionInLines() : Array<ProductionLine> {
      return this._productionInLines;
   }

   public get productionOutLines() : Array<ProductionLine> {
      return this._productionOutLines;
   }

   public get productionTotal() : number {
      return this._productionTotal;
   }

   public get productionInTotal() : number {
      return this._productionInTotal;
   }

   public get productionOutTotal() : number {
      return this._productionOutTotal;
   }

   public get production() : number {
      return this._production;
   }

   public set production(production: number) {
      this._production = production;
   }

   public addProductionLine(productionLine: ProductionLine){
      if (productionLine.product.id !=  this._product.id){
         throw new Error(
            'Invalid product line for this product'
          );
      }

      this._productionAllLines.push(productionLine);
      this._productionTotal = this._productionTotal + productionLine.production;
      if (productionLine.production < 0){
         this._productionOutLines.push(productionLine);
         this._productionOutTotal = this._productionOutTotal + productionLine.production;
      }else{
         this._productionInLines.push(productionLine);
         this._productionInTotal = this._productionInTotal + productionLine.production;
      }
   }
}