import { Component, OnInit, NgZone, Input } from '@angular/core';
import { ProductionControlService } from 'src/services/production-control.service';
import { ProductionProduct } from 'src/models/production-product.model';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-produccion-component',
  templateUrl: './produccion.component.html',
  styleUrls: ['./produccion.component.css']
})
export class ProduccionComponent implements OnInit {

   @Input('useOwnedProducts') useOwnedProducts: boolean = true;
   @Input('mode') mode : string = "in"; // possible values, in (means the production value entered will be added), out (means the production value will be substracted), stock (means the production value will be the new total in stock)

   private _processing: boolean = false;
   private _allProduction: Array<ProductionProduct> = [];
   private _col2Title: string;

   constructor(private productionControlService: ProductionControlService, private ngZone: NgZone) {
   }

   public get allProduction(){
      return this._allProduction;
   }

   public get processing(){
      return this._processing;
   }

   public get col2Title(){
      return this._col2Title;
   }

   ngOnInit() {
      if (this.useOwnedProducts){
         this._col2Title = this.mode == "in" ? "Producido" : (this.mode == "out" ? "Perdido" : "Inventario");
      }else{
         this._col2Title = this.mode == "in" ? "Entrada" : (this.mode == "out" ? "Sobrante" : "Inventario");
      }

      this.productionControlService.initProductionServiceService().then(()=>{
         this.ngZone.run(() => {
            this._allProduction = this.useOwnedProducts ? this.productionControlService.allProductionOwned : this.productionControlService.allProductionExternal;
         });}
      );
   }

   public saveProduction(){
      this.productionControlService.saveProduction(this.mode).then(()=>{
         this.ngZone.run(() => {
            this._allProduction = this.useOwnedProducts ? this.productionControlService.allProductionOwned : this.productionControlService.allProductionExternal;
         });}
      );
   }
}