import { Injectable } from '@angular/core';
import { GapiSession } from 'src/infrastructure/sessions/gapi.session';
import { ProductService } from './product.service';
import { BaseProductionService } from './production-base.service';
import { ActivatedRoute } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class ProductionControlService extends BaseProductionService {

   private static SHEET_NAME = 'ControlEntradaSalidaProductos';
   private static RANGE_PRODUCTION = ProductionControlService.SHEET_NAME + '!A2:E';
   private static RANGE_PRODUCTION_ADD_ROW = ProductionControlService.SHEET_NAME + '!A';

   constructor(gapiSession: GapiSession, productService: ProductService, route: ActivatedRoute) {
      super(gapiSession, productService, route);
   }

   public getRange(): string{
      return ProductionControlService.RANGE_PRODUCTION;
   }

   public getRangeForAddingNewRow(): string {
      return ProductionControlService.RANGE_PRODUCTION_ADD_ROW + (this.totalOfLines + 2); // starts in A2
   }

   public useOwnedProducts(): boolean {
      return true;
   }
}